import { mapGetters } from "vuex"
import {
    friendTaskAddress,
} from "@/jsons/contractAddress.js"
import AbiTask from "@/jsons/abi-friendTask.js"
import Decimal from "decimal.js"

export default {
    data: () => ({
        taskList: [],
        towDataList: [],
        receivedList: [],
        taskCount: 0,
        clickLoading: false,
        listLoading: false,
        fee:'',
    }),
    computed: {
        ...mapGetters({
            web3Provider: 'web3Provider',
            // ethAddress: 'ethAddress',
        }),
        filterList() {
            return this.towDataList.filter(item => parseInt(item.describe) <= this.taskCount && item.received == false)
        },
    },
    mounted() {
        this.init()
    },
    methods: {
        getContractTask() {
            return new this.web3Provider.eth.Contract(AbiTask, friendTaskAddress)
        },
        async init() {
            if (!this.web3Provider) return;
            // console.log(addressList)
            // this.resChild
            try {
                this.listLoading = true;
                this.taskCount = await this.get_task_tow_count();
                this.towDataList = await this.getTask_tow_data();
                this.listLoading = false;
            } catch (e) {
                console.log(e);
                this.listLoading = false;
            }
        },
        async getTask_tow_data() {
            const contractTask = this.getContractTask();
            const address = await this.web3Provider.eth.getAccounts();
            const data = await contractTask.methods.getTask_tow_data().call();
            const receivedList = await contractTask.methods.getTask_tow_Award_data(address[0]).call();
            const fee = await contractTask.methods.getFee().call();
            const feeAmount = this.web3Provider.utils.fromWei(fee);
            // console.log('getTask_tow_data', data,feeAmount)
            let list = [];
            data.map((item, index) => {
                // console.log('getTask_tow_data', item[0], item[1])
                let _item = {
                    describe: item[0],
                    amount: item[1],
                    received: false,
                    id: index,
                    feeAmount:0,
                }
                _item.feeAmount = _item.amount * parseFloat(feeAmount)
                receivedList.map(receivedItem => {
                    if (receivedItem[0] == _item.describe && receivedItem[1] == _item.amount) _item.received = receivedItem[2]
                })
                list.push(_item)
            })
            // console.log('towDataList', list[0])
            return list;
        },
        async get_task_tow_count() {
            const contractTask = this.getContractTask();
            const address = await this.web3Provider.eth.getAccounts();
            const data = await contractTask.methods.get_task_tow_count(address[0]).call();
            console.log("get_task_tow_count", data);
            return data;
        },
        async withdrawalCardTow2(item) {
            try {
                this.clickLoading = true;
                const contractTask = this.getContractTask();
                const address = await this.web3Provider.eth.getAccounts();
                const fee = await contractTask.methods.getFee().call();
                let amount = new Decimal(fee).mul(new Decimal(item.amount)).toString()
                const data = await contractTask.methods.withdrawalCardTow2(address[0], item.id, amount).send({
                    from: address[0],
                    value: amount
                });
                this.init();
                this.clickLoading = false;
            } catch (e) {
                console.log(e);
                this.clickLoading = false;
            }
        }
    }
}
